import React from 'react';
import { Routes, Route, Navigate, BrowserRouter } from 'react-router-dom';
import { Box, ThemeProvider, createTheme, CssBaseline } from '@mui/material';
import Dashboard from './components/Dashboard';
import CropAnalysis from './components/CropAnalysis';
import Navbar from './components/Navbar';
import { AuthProvider, useAuth } from './contexts/AuthContext';
import Login from './components/Login';
import Signup from './components/Signup';

const ProtectedRoute = ({ children }: { children: React.ReactNode }): React.ReactElement => {
  const { isAuthenticated } = useAuth();
  return isAuthenticated ? <>{children}</> : <Navigate to="/login" />;
};

function App() {
  const theme = createTheme({
    components: {
      MuiButton: {
        styleOverrides: {
          root: {
            minHeight: 48, // Better touch targets
            fontSize: '1rem'
          }
        }
      },
      MuiBottomNavigation: {
        styleOverrides: {
          root: {
            height: 64 // Larger bottom navigation for mobile
          }
        }
      }
    }
  });

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <BrowserRouter>
        <AuthProvider>
          <Box sx={{ 
            display: 'flex', 
            flexDirection: 'column', 
            minHeight: '100vh',
            width: '100%',
            overflow: 'hidden',
            WebkitOverflowScrolling: 'touch' // Smooth scrolling on iOS
          }}>
            <Navbar />
            <Box component="main" sx={{ 
              flexGrow: 1,
              padding: { xs: 1, sm: 2, md: 3 }, // Responsive padding
              overflowY: 'auto'
            }}>
              <Routes>
                <Route path="/login" element={<Login />} />
                <Route path="/signup" element={<Signup />} />
                <Route path="/" element={<ProtectedRoute><Dashboard /></ProtectedRoute>} />
                <Route path="/crop-analysis" element={<ProtectedRoute><CropAnalysis /></ProtectedRoute>} />
                <Route path="*" element={<Navigate to="/" />} />
              </Routes>
            </Box>
          </Box>
        </AuthProvider>
      </BrowserRouter>
    </ThemeProvider>
  );
}

export default App;